.infoBlok {
  margin-top: 20px;
  height: 30px;
  width: 100%;
  color: rgb(241, 35, 35);
  font-size: 14px;
  font-weight: 500;
  line-height: 0px;
}
form {
  display: flex;
  flex-direction: column;
}
label {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-size: 19px;
  line-height: 30px;
}
.input {
  margin-top: 5px;
  width: 100%;
  height: 35px;
  font-size: large;
  font-weight: bold;
}
.submit {
  margin-top: 5px;
  background: #000000;
  color: aliceblue;
  border: none;
  width: 130px;
  height: 45px;
  align-self: center;
  /* margin: 0 auto; */
  cursor: pointer;
}
